<template>
    <div class="loading">
        <Icon type="load-a" size="48" class="loading"></Icon>
    </div>
</template>

<script>
export default {
  name: 'loading',
  data() {
    return {}
  },
}
</script>
<style scoped>
.loading {
  width: 100%;
  height: 100%;
  vertical-align: middle;
  text-align: center;
}

.loading img {
  width: 120px;
  display: block;
  margin-top: -60px;
  margin-left: -35px;
  position: absolute;
  left: 50%;
  top: 50%;
}
</style>


