<template>
    <div class="home">
        <!-- header -->
        <Vheader></Vheader>
        <!-- article -->
        <section class="indexContent">
     
            <div class="contentMain clearfix">
                <section class="menu">
                    <menu class="menu-name">
                        <li>
                            <router-link :to="langType!='cn'?'/'+langType+'/hot':'/hot'">{{lang.popular}}</router-link>
                        </li>
                        <li>
                            <router-link :to="langType!='cn'?'/'+langType+'/nation':'/nation'">{{lang.subject}}</router-link>
                        </li>
                        <li>
                            <router-link :to="langType!='cn'?'/'+langType+'/profess':'/profess'">{{lang.job_title}}</router-link>
                        </li>
                        <li>
                            <router-link :to="langType!='cn'?'/'+langType+'/subject':'/subject'" style="letter-spacing:0;padding-left:0;">{{lang.country}}</router-link>
                        </li>
                        <li>
                            <router-link :to="langType!='cn'?'/'+langType+'/territory':'/territory'">{{lang.area}}</router-link>
                        </li>
                    </menu>
                </section>
                <section class="content position">
                    <!-- loading -->
                  <!--   <Loading v-if="getLoadingStatus" class="indexLoading"></Loading> -->
                    <!-- 二级路由 -->
                    <router-view></router-view>
                </section>
            </div>
        </section>
  
    </div>
</template>
<script>
import Vheader from '../common/header.vue'; //引入头部
import Vfooter from '../common/footer.vue' ;//引入底部 
import Loading from '../common/loading.vue';
export default {
    name: 'home',
    data() {
        return {
            lang:{},
            langType:"",
             htmlTitle: '页面导出PDF文件名'
        }
    },
    components: {
        Vheader, Vfooter, Loading
    },
    computed: {
        getLoadingStatus: function () { //loading..
            return this.$store.state.fetchLoading;
        }
    },
    methods: {
        selectMenu: function (value, index) {
            this.activeValue = index;//选中的效果
            alert('选中..');
        },
        toggleLang(){
            const _this = this;
            this.commonMethods.selectLangType(function(langType){
                _this.lang = _this.langOption[langType];
                _this.langType = langType;
            })
        }
    },
    watch:{
        "$route.fullPath":{
            handler(){
                this.toggleLang();
            }
        }
    },
    mounted(){
        this.toggleLang();
    }
}
</script>




